import { Schema } from 'mongoose';
import { schemaComposer } from 'graphql-compose';
import { convertSchemaToGraphQL } from 'graphql-compose-mongoose';
import { ICountry, countrySchema } from '../country';

export interface IUserSettings {
  _id?: any;
  restrictedUser: ICountry[];
}

export const userSettingsSchema = new Schema<IUserSettings>({
  restrictedUser: [countrySchema],
});

convertSchemaToGraphQL(userSettingsSchema, 'userSettings', schemaComposer);
